html, body {
  height: 100%;
}

.container {
  display: grid;
  grid-template-areas: "header header header" "nav nav nav" "sideA contents sideB" "footer footer footer";
  width: 100%;
  min-height: 100%;
  grid-template-rows: auto auto 1fr auto;
  grid-template-columns: auto 1fr auto;
}

@media only screen and (max-width: 767px) {
  .container {
    grid-template-areas: "header" "sideA" "sideB" "contents" "nav" "footer";
    grid-template-rows: auto auto auto 1fr auto auto;
    grid-template-columns: 1fr;
  }
}
.layout {
  border: 1px solid red;
}

.header {
  grid-area: header;
  height: 100px;
}

@media only screen and (max-width: 767px) {
  .header {
    display: none;
  }
}
.footer {
  grid-area: footer;
  height: 150px;
}

.contents {
  grid-area: contents;
}

.sideA {
  grid-area: sideA;
  background: Green;
}

.sideB {
  grid-area: sideB;
}

.nav {
  grid-area: nav;
  height: 50px;
  background: #000;
  color: #fff;
}

body,
html {
  width: 100%;
  height: 100%;
  background-color: #111414;
  color: #efefef;
}

a {
  text-decoration: none;
  color: inherit;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 20px 40px;
  background-color: transparent;
  backdrop-filter: blur(8px);
  transition: top 0.3s ease-in-out;
}

.header.hide {
  top: -100%;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav .header__logo {
  font-weight: 700;
  font-size: 2em;
}

.header__nav .header__list {
  display: flex;
}

.header__nav .header__item {
  font-size: 1.4em;
  margin-right: 20px;
}

.header__nav .header__item:last-of-type {
  margin-right: 0;
}

.main section,
footer section {
  padding: 50px 10%;
}

.main__section.intro {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  text-align: center;
}

.main__section.intro .intro__content .intro__title {
  font-size: 5rem;
}

.main__section.intro .intro__content .intro__imgbox {
  width: 370px;
  height: 420px;
  margin: auto;
  background-color: #ddd;
  border: 1px solid #000;
  border-radius: 30px;
}

.main__section.intro .intro__content .intro__subtitle {
  font-size: 2rem;
}

.main__section.works .works__list .works__item {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 70px 0;
  border-bottom: 1px solid rgba(200, 200, 200, 0.2);
}

.main__section.works .works__list .works__item .works__info .works__title {
  font-size: 3.8em;
  line-height: 1.8;
}

.main__section.works .works__list .works__item .works__info .works__keywords {
  font-size: 1.4em;
}

.main__section.works .works__list .works__item .works__info .works__keywords .works__kinds {
  color: rgba(239, 239, 239, 0.6823529412);
  font-weight: 300;
  padding: 4px 6px;
}

.main__section.works .works__list .works__item .works__info .works__keywords .works__kinds:last-of-type {
  position: relative;
  padding-left: 20px;
  border: none;
}

.main__section.works .works__list .works__item .works__info .works__keywords .works__kinds:last-of-type::before {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  content: "|";
}

.main__section.works .works__list .works__item .works__year .works__period {
  font-size: 1.6em;
  font-weight: 300;
  color: rgba(239, 239, 239, 0.6823529412);
}

.main__section.education {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 100px 10%;
}

.main__section.education .edu__info {
  max-width: 40%;
}

.main__section.education .edu__info .edu__title {
  font-size: 3.8em;
  line-height: 1.5;
  margin-bottom: 30px;
}

.main__section.education .edu__info .edu__desc {
  font-size: 1.6em;
  font-weight: 400;
}

.main__section.education .edu__list__wrap {
  min-width: 50%;
}

.main__section.education .edu__list__wrap .edu__list {
  text-align: left;
  font-size: 1.4em;
  font-weight: 300;
}

.main__section.education .edu__list__wrap .edu__list .edu__item {
  display: flex;
  align-items: center;
}

.main__section.education .edu__list__wrap .edu__list .edu__item .edu__period {
  min-width: 230px;
}

.main__section.education .edu__list__wrap .edu__list + .edu__list {
  margin-top: 50px;
}

.main__section.skills {
  padding: 100px 0;
  overflow: hidden;
}

.main__section.skills .skills__list {
  display: flex;
  flex-wrap: nowrap;
  animation: scroll 1s linear infinite alternate-reverse;
  overflow: hidden;
}

@keyframes scroll {
  0% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-10%);
  }
}
.main__section.skills .skills__item {
  max-width: 200px;
  margin-right: 60px;
}

.main__section.skills .skills__img {
  width: 100%;
  vertical-align: top;
}

.main__section.contact .contact__content {
  position: relative;
}

.main__section.contact .contact__content .contact__image {
  position: absolute;
  top: 0;
  width: 6em;
  height: 6em;
}

.main__section.contact .contact__content .contact__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
}

.main__section.contact .contact__content .contact__image::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 1.2em;
  height: 1.2em;
  background-color: #c084fc;
  border: 0.125em solid #efefef;
  border-radius: 50%;
  z-index: 90;
  content: "";
}

.main__section.contact .contact__content .contact__title {
  text-indent: 2em;
  font-size: 3.8em;
  max-width: 60%;
  word-break: keep-all;
  margin-bottom: 20px;
}

.main__section .contact__mail {
  font-size: 2.39em;
  color: #c084fc;
  padding: 20px 0;
}

.section__title {
  position: relative;
  font-size: 2.39em;
  padding: 30px 0;
  transition: top 0.3s ease-in-out;
}

.section__title sup {
  font-size: 0.6em;
  color: #fbbf24;
}

.section__title::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: transparent;
  border-bottom: 1px solid rgb(255, 255, 255);
  animation: drawBorder 1s linear forwards;
  content: "";
}

.footer .footer__copyright {
  position: relative;
  padding-top: 30px;
  color: rgba(255, 255, 255, 0.5);
}

.footer .footer__copyright::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: transparent;
  border-bottom: 1px solid rgb(255, 255, 255);
  animation: drawBorder 1s linear forwards;
  content: "";
}

@keyframes drawBorder {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}