.glowBlue {
  position:relative;
  z-index:1;
  &:after {
    content:"";
    width:100%;
    height:50%;
    display: block;
    position:absolute;
    background:#34a0e1;
    left:0;
    bottom:0;
    z-index:-1;
  }
}

